import { readGlobalVerdict, readLatestSnapshot, readRiskTruthPanel } from "@/lib/server/data";

function toNum(v: unknown, fallback = 0) {
  const n = Number(v);
  return Number.isFinite(n) ? n : fallback;
}

export default async function MetodologiaPage() {
  const [snap, verdict, panel] = await Promise.all([
    readLatestSnapshot(),
    readGlobalVerdict(),
    readRiskTruthPanel(),
  ]);

  const summary = (snap?.summary || {}) as Record<string, unknown>;
  const checks = (verdict?.gate_checks || {}) as Record<string, unknown>;
  const scores = (verdict?.scores || {}) as Record<string, unknown>;
  const counts = (panel?.counts || {}) as Record<string, unknown>;

  return (
    <div className="p-5 md:p-6 lg:p-8 space-y-6">
      <section className="rounded-2xl border border-zinc-800 bg-zinc-950/50 p-5">
        <p className="text-xs tracking-[0.14em] uppercase text-zinc-500">Nivel 2 - Metodo e auditoria</p>
        <h1 className="mt-2 text-2xl md:text-3xl font-semibold text-zinc-100">Metodologia e evidencias do run atual</h1>
        <p className="mt-3 text-sm text-zinc-300">
          Todos os numeros abaixo sao carregados de artefatos reais do repositorio: snapshot validado, painel de verdade de risco e gate global.
        </p>
      </section>

      <section className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-3">
        <Metric title="Global verdict" value={String(verdict?.status || "unknown").toUpperCase()} />
        <Metric title="Run id" value={String(snap?.runId || "n/a")} />
        <Metric title="Ativos no painel" value={String(toNum(counts.assets, 0))} />
        <Metric title="Validated" value={String(toNum(counts.validated, 0))} />
      </section>

      <section className="rounded-2xl border border-zinc-800 bg-zinc-950/45 p-5">
        <h2 className="text-lg font-semibold text-zinc-100">Gate checks</h2>
        <div className="mt-3 grid grid-cols-1 md:grid-cols-2 gap-2 text-sm">
          {Object.keys(checks).length ? (
            Object.entries(checks).map(([k, v]) => (
              <div key={k} className="rounded-lg border border-zinc-800 bg-black/25 p-2 text-zinc-300">
                <span className="text-zinc-400">{k}:</span> {String(v)}
              </div>
            ))
          ) : (
            <div className="text-zinc-400">Sem gate_checks no arquivo de veredito.</div>
          )}
        </div>
      </section>

      <section className="rounded-2xl border border-zinc-800 bg-zinc-950/45 p-5">
        <h2 className="text-lg font-semibold text-zinc-100">Scores e contorno</h2>
        <div className="mt-3 grid grid-cols-1 md:grid-cols-3 gap-2 text-sm">
          {Object.keys(scores).length ? (
            Object.entries(scores).map(([k, v]) => (
              <div key={k} className="rounded-lg border border-zinc-800 bg-black/25 p-2 text-zinc-300">
                <span className="text-zinc-400">{k}:</span> {String(v)}
              </div>
            ))
          ) : (
            <div className="text-zinc-400">Sem scores no arquivo de veredito.</div>
          )}
          <div className="rounded-lg border border-zinc-800 bg-black/25 p-2 text-zinc-300">
            <span className="text-zinc-400">deployment_gate.blocked:</span>{" "}
            {String(((summary.deployment_gate as Record<string, unknown> | undefined)?.blocked ?? "n/a"))}
          </div>
          <div className="rounded-lg border border-zinc-800 bg-black/25 p-2 text-zinc-300">
            <span className="text-zinc-400">status:</span> {String(summary.status || "n/a")}
          </div>
        </div>
      </section>

      <section className="rounded-2xl border border-zinc-800 bg-zinc-950/45 p-5">
        <h2 className="text-lg font-semibold text-zinc-100">Caveats de uso</h2>
        <ul className="mt-3 space-y-2 text-sm text-zinc-300">
          <li>1. O motor e radar de regime e risco, nao previsao garantida de preco.</li>
          <li>2. Sinal inconclusivo deve ficar em modo diagnostico, sem acao automatica.</li>
          <li>3. Backtest e replay nao garantem resultado futuro.</li>
          <li>4. Custos de transacao e latencia podem degradar desempenho operacional.</li>
          <li>5. Decisao final exige contexto de dominio e governanca de risco.</li>
        </ul>
      </section>
    </div>
  );
}

function Metric({ title, value }: { title: string; value: string }) {
  return (
    <div className="rounded-xl border border-zinc-800 bg-zinc-950/55 p-3">
      <div className="text-xs uppercase tracking-[0.12em] text-zinc-500">{title}</div>
      <div className="mt-2 text-lg font-semibold text-zinc-100">{value}</div>
    </div>
  );
}
